// BASIC
import React, { Component } from 'react'
import styled, { css, keyframes } from 'styled-components'
// STYLES
import { BrowserLink } from '../../Styles/Components'
// COMPONENTS
import Delete from './Delete'
// IMAGES
import star from '../../Images/star.svg'
import tick from '../../Images/tick.svg'

const PictureWrapper = styled.div`
	position: relative;
	/* animation: scaleIn 1s ease; */
	@media(max-height: 450px) {
		display: none;
	}
`
const PictureElement = styled.div`
	width: 90vw;
	height: calc(100vw / 1.618);
	max-height: calc(100vh - 341px);
	margin: 0 auto;
	border-radius: 20px;
	overflow: hidden;
	${props =>
		props.deleteImage &&
		css`
			animation: scaleOut 0.5s both;
		`
	}
	@media(min-width: 700px) {
		width: 647px;
		height: 400px;
	}
`
const Image = styled.img`
	width: 100%;
	height: auto;
	${props =>
		props.longImage &&
		css`
			width: auto;
			height: 100%;
		`
	}
	${props =>
		props.entry &&
		css`
			animation: scaleIn 0.5s ease;
		`
	}
`
const animation = keyframes`
	0% {
		opacity: 0;
		transform: translate(-50%, -50%) rotate(-5deg) scale(0.7);
	}
	40% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1.2);
	}
	60% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1);
	}
	80% {
		opacity: 1;
		transform: translate(-50%, -50%) rotate(0) scale(1.1);
	}
	100% {
		opacity: 0;
		transform: translate(-50%, -50%) rotate(5deg) scale(0.7);
	}
`
const Animation = styled.img`
	opacity: 0;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 60%;
	height: 60%;
	transform: translate(-50%, -50%);
	animation: ${animation} 0.7s 0.3s ease;
`

class Picture extends Component {
	state = {
		longImage: false,
		deleteImage: true,
		animatedImage: false
	}
	render() {
		return (
			<PictureWrapper>
				<Delete up={this.state.deleteImage}
					onClick={
						() => {
							if (this.state.deleteImage === false) {
								this.setState({deleteImage: true, animatedImage: false})
							}
							else {
								this.setState({deleteImage: false, animatedImage: true})
							}
						}
					}
				/>
				<PictureElement id="imgElement" deleteImage={this.state.deleteImage} src={this.props.src}>
					<BrowserLink href={this.props.link} target="_blank">
						<Image entry={this.state.animatedImage} id="img" src={this.props.src} alt={`${this.props.word} image`} title={`${this.props.word} image`}
							delete={this.state.deleteImage}
							longImage={this.state.longImage}
						/>
					</BrowserLink>
				</PictureElement>
				{this.props.animation ? <Animation src={this.props.animation === 'great' ? star : this.props.animation === 'good' ? tick : ''} /> : ''}
			</PictureWrapper>
		);
	}
}

export default Picture